iv v-if=\"!isedit\">\n         <span>:</span>\n         <i>{{total}}</i>\n      </div>\n      <template v-if=\"isedit\">\n         <div class=\"deleteorcollect\">\n            <button type=\"button\" v-on:click=\"delete\"></button>\n            <button type=\"button\" v-on:click=\"collection\"></button>\n         </div>\n      </template>\n      <button v-else v-on:click=\"account\">({{buynum}})</button>\n   </footer>\n   <!-- dialong -->\n   <!--  dialog -->\n   <div v-if=\"islog\" class=\"yo-dialog yo-dialog-test\">\n       <div class=\"bd\">\n           <p>~</p>\n       </div>\n       <div class=\"ft\">\n           <button class=\"yo-btn yo-btn-dialog yo-btn-l\" v-on:click=\"cancle\"></button>\n           <button class=\"yo-btn yo-btn-dialog yo-btn-l\" v-link=\"{path:'/'}\"></button>\n       </div>\n   </div>\n   <!-- dialog -->\n   <div v-if=\"iscollect\" class=\"yo-dialog yo-dialog-test\">\n       <div class=\"bd\">\n           <p>~</p>\n       </div>\n       <div class=\"ft\">\n           <button class=\"yo-btn yo-btn-dialog yo-btn-l\" v-on:click=\"confirmcollect\"></button>\n       </div>\n   </div>\n   <!--  -->\n   <div v-if=\"ismark\" class=\"yo-mask\"></div>\n</div>\n";


