let commonUtil = {
  render(str) {
    let container = document.querySelector('body');
    container.innerHTML = str + container.innerHTML;
  },
  isAlloaded(scope,cb){
    var t_img;//
    var isLoad = true;//
    //
    return isImgLoad(cb);
    //
    function isImgLoad(callback){
      // 
      $(scope).find('img').each(function(){
        //0isloadfalse each
        if (this.height ===0) {
          isLoad = false;
          return false;
        }
      });
      // true 0 
      if(isLoad){
        clearTimeout(t_img);//
        // 
        callback(scope);
        //false  
      }else {
        isLoad = true;
        t_img =setTimeout(function() {
          isImgLoad(callback);//
        },500);//
      }
    }
  },
  dirScroll() {
    var that = this;
    // 
    Vue.directive('scroll', function (value) {
        if(value){
          that.isAllLoaded('#classify-scroll', function() {
            new IScroll(value);
          });
        }
    });
  }
};

export default commonUtil;



